@import "normalizes.scss";
@import "resets.scss";
@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@200;300;400;600&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500&display=swap');
$main-color-one: #5E81A0;
$main-color-two: #5B6B6B;
$main-color-three: #C0CBCC;
$main-font: Montserrat;

* {
    box-sizing: border-box; }

html {
    font-family: Montserrat;
    font-style: normal;
    font-weight: 400; }

.container {
    max-width: 1240px;
    margin: 0 auto; }
.main-section {
    position: relative;
    min-height: 800px;
    background: #F6F3F0;
    overflow: hidden;
    &__nav-container {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        height: 108px;
        align-items: center;
        padding-top: 26px;
        padding-left: 90px; }
    &__content-container {
        display: flex;
        flex-direction: row;
        position: relative; }
    &__logo {
        width: 150px; }
    &__first-circle {
        position: absolute;
        top: 48px;
        left: 1027px; }
    &__second-circle {
        position: absolute;
        right: -120px;
        top: 650px; } }
.nav-menu {
    &__list {
        display: flex;
        flex-direction: row; }
    &__list-item {
        margin-right: 107px;
        font-size: 18px;
        font-weight: 600;
        &:last-child {
            margin-right: 0px; }
        a {
            text-decoration: none;
            color: black; } } }
.left-side {
    position: relative;
    padding-top: 35px;
    margin-left: 108px;
    &__img {
        position: relative;
        z-index: 2; }
    &__figure-one {
        position: absolute;
        top: 120px;
        left: -50px;
        width: 577.67px;
        height: 220px;
        background: $main-color-two;
        border-radius: 100px;
        box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
        transform: rotate(-64.14deg); }
    &__figure-two {
        position: absolute;
        z-index: 1;
        top: 162px;
        left: -15px;
        width: 630px;
        height: 220px;
        background: $main-color-one;
        border-radius: 100px;
        box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
        transform: rotate(-45deg); }
    &__figure-three {
        position: absolute;
        top: 212px;
        left: 32px;
        width: 579.15px;
        height: 220px;
        background: $main-color-three;
        border-radius: 100px;
        box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
        transform: rotate(-29.27deg); } }

.right-side {
    padding-top: 120px;
    margin-left: 798px;
    position: absolute;
    &__text {
        font-size: 18px;
        padding-top: 10px;
        width: 266px;
        height: 77px;
        position: relative;
        z-index: 2; }
    &__title {
        z-index: 2; }
    &__icon-box {
        margin-bottom: 22px; } }

.title {
    width: 314px;
    font-size: 64px;
    font-weight: 600;
    position: relative;
    &__hr {
        position: absolute;
        margin: 0;
        width: 102px;
        border: none;
        border-top: 1px solid #000000;
        &_top {
            bottom: 125px; } } }

.icons {
    display: flex;
    flex-direction: row;
    &__items {
        margin-right: 14px;
        z-index: 0;
        width: 35px; }
    &:last-child {
        margin-right: 0px; } }

.button {
    display: flex;
    flex-direction: row;
    filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
    &__main-btn {
        margin-right: 35px;
        width: 200px;
        height: 60px;
        border-radius: 100px;
        background: #42423C;
        border: 0;
        font-family: Roboto;
        font-size: 18px;
        font-weight: 500;
        color: #FFFF; }
    &__icon-btn {
        display: inline-flex;
        align-items: center;
        img {
            margin-right: 12px; }
        p {
            font-size: 18px;
            font-weight: 400; } } }

.circle {
    width: 224.03px;
    height: 224.03px;
    background: linear-gradient(279.08deg, #555252 6.35%, #5E81A0 48.64%, #F6F3F0 93.65%);
    border: none;
    border-radius: 50%;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    display: flex;
    justify-content: center;
    align-items: center;
    &_big {
        width: 282px;
        height: 282px; }
    &_small {
        width: 180.17px;
        height: 180.17px; }
    &__inner {
        width: 160px;
        height: 160px;
        background: #F6F3F0;
        border-radius: 50%;
        &_big {
            width: 188px;
            height: 188px; }
        &_small {
            width: 128.68px;
            height: 128.68px; }
        &_white {
            background: #FFFF; }
        &_small-white {
            width: 128.68px;
            height: 128.68px;
            background: #FFFF; } } }


.second-section {
    min-height: 700px;
    position: relative;
    &__container {
        padding-top: 23px;
        position: relative; }
    &__title {
        padding-left: 50px;
        margin-bottom: 38px; }
    &__first-circle {
        position: absolute;
        top: 335px;
        left: -18px;
        z-index: -1; }
    &__second-circle {
        position: absolute;
        top: 73px;
        right: 2px;
        z-index: -1; } }

.cards {
    display: flex;
    justify-content: center;
    margin-bottom: 90px;
    &__item {
        width: 333px;
        height: 363px;
        position: relative;
        overflow: hidden;
        box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
        border-radius: 15px; }
    &__text {
        position: absolute;
        font-style: normal;
        font-weight: 700;
        font-size: 32px;
        line-height: 39px;
        text-align: center;
        color: #000000;
        transform: rotate(89.65deg); } }

.first-card {
    background: $main-color-three;
    margin-right: 65px;
    img {
        width: 414.27px;
        height: 1000px;
        position: absolute;
        top: 15px;
        left: -150px; }
    p {
        top: 145px;
        left: 170px; } }

.second-card {
    background: $main-color-one;
    margin-right: 65px;
    img {
        width: 238px;
        height: 716px;
        position: absolute;
        top: 15px;
        left: 3px; }
    p {
        top: 166px;
        left: 190px; } }

.third-card {
    background: #95B2C8;
    margin-right: 0;
    img {
        width: 108px;
        height: 375px;
        position: absolute;
        top: 18px;
        left: 59px; }
    p {
        top: 155px;
        left: 134px; } }

.icons-text {
    display: flex;
    justify-content: space-evenly;
    &__item {
        display: flex;
        justify-content: center;
        align-content: center;
        &:last-child {
            margin-right: 0; }
        img {
            width: 70px;
            height: 70px;
            margin-right: 35px; }
        p {
            font-family: Roboto;
            font-size: 20px;
            font-weight: 400;
            span {
                font-size: 36px;
                font-weight: 600; } } } }

.third-section {
    min-height: 700px;
    background: #F6F3F0;
    position: relative;
    z-index: 0;
    &__container {
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-between;
        padding-left: 227px;
        padding-top: 100px;
        position: relative; }
    &__right-side {
        max-width: 530px; }
    &__title-left {
        z-index: 2; }
    &__title-right {
        margin-bottom: 40px; }
    &__text {
        margin-bottom: 40px;
        font-size: 18px; }
    &__first-circle {
        position: absolute;
        top: 237px;
        left: 136px;
        z-index: 0; }
    &__second-circle {
        position: absolute;
        transform: matrix(-0.25, 0.97, -0.97, -0.25, 0, 0);
        top: 270px;
        left: 52px;
        z-index: -1; } }

.footer {
    position: relative;
    &__img {
        position: absolute;
        width: 670px;
        left: -1px;
        bottom: 0px;
        margin: 0; }
    &__title {
        width: 400px;
        margin-bottom: 44px; }
    &__container {
        min-height: 350px;
        display: flex;
        align-items: center;
        justify-content: flex-end;
        padding-right: 82px; } }















